import React from "react";

import { useNotification } from "providers/Notification";
import ProgressFallback from "components/fallbacks/Progress";

import Notification from "./Notification";
import css from "./style.module.scss";

const RewardNotifications = () => {
  const { notification, missed } = useNotification();

  const { rewards, notifications } = notification;

  return (
    <div className={css.root}>
      {!rewards.length && !notifications.length && !!missed && (
        <ProgressFallback color="#6F99EB" size={75} />
      )}

      {rewards.map((reward) => {
        const { id, title, description, amount, coin, viewed } = reward;

        return (
          <React.Fragment key={id}>
            <Notification
              isNew={viewed === "0"}
              title={title}
              description={description}
              amount={amount}
              coin={coin}
              id={id}
            />
          </React.Fragment>
        );
      })}
    </div>
  );
};

export default RewardNotifications;
