import React, {
  FunctionComponent,
  Suspense,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import clsx from "clsx";

import { useWhiteTheme, useWindowSize } from "providers";

import Footer from "containers/Page/components/Footer";
import { useNotification } from "providers/Notification";
import NotificationWrapper from "components/NotificationWrapper";
import LazyComponentLoader from "config/LazyComponentLoader";

import css from "./style.module.scss";

const EventDecoration = React.lazy(() =>
  LazyComponentLoader(import("./EventDecoration")),
);
const EventGrow = React.lazy(() => LazyComponentLoader(import("./EventGrow")));

const timeouts: NodeJS.Timeout[] = [];
const clearTimeouts = () => timeouts.forEach((t) => clearTimeout(t));

export const Content: FunctionComponent = ({ children }) => {
  const { height, width } = useWindowSize();
  const { activeEvent } = useWhiteTheme();

  const [isNotificationDisabled, setIsNotificationDisabled] = useState(true);
  const [showAnimation, setShowAnimation] = useState(false);

  const mainRef = useRef<HTMLElement>(null);

  const rootStyle = useMemo(
    () => ({
      minHeight: `${width <= 600 ? height - 90 : height}px`,
    }),
    [height],
  );

  const { notification, setNotification } = useNotification();
  const handleClose = () => {
    setNotification({
      ...notification,
      isOpen: false,
    });
  };

  useEffect(() => {
    clearTimeouts();
    if (notification.isOpen) {
      setIsNotificationDisabled(false);
      const t = setTimeout(() => {
        setShowAnimation(true);
      }, 0);
      timeouts.push(t);
    } else {
      setShowAnimation(false);
      const t = setTimeout(() => {
        setIsNotificationDisabled(true);
      }, 500);
      timeouts.push(t);
    }
  }, [notification.isOpen]);

  return (
    <div className={css.root} style={rootStyle}>
      {activeEvent && (
        <Suspense fallback={null}>
          <EventDecoration eventName={activeEvent} />
          <EventGrow eventName={activeEvent} />
        </Suspense>
      )}

      {width > 600 && !isNotificationDisabled && (
        <div
          className={clsx(css.notification, {
            [css.enable]: showAnimation,
          })}>
          <div onClick={handleClose} className={css.notificationCloseOverlay} />
          <NotificationWrapper />
        </div>
      )}
      <main ref={mainRef} className={css.content}>
        {children}
      </main>
      <Footer />
    </div>
  );
};

export default Content;
