import React, { useMemo } from "react";
import { useHistory } from "react-router-dom";
import clsx from "clsx";

import { useGetLocale, useWindowSize } from "providers";
import { Menu, MenuItem, MenuProps } from "components";
import { useLocalStorage } from "hooks";
import { fetchBase } from "utils";

import { menuItems } from "./config";
import css from "./style.module.scss";

const withoutFeyorraWidth = parseInt(css.feyorraDisableSize);

const CustomMenu = ({ onClose, isOpen, anchor }: MenuProps) => {
  const [token, setToken] = useLocalStorage("token");
  const [, setExpirationTime] = useLocalStorage("expirationTime");
  const history = useHistory();

  const { isMobile, width } = useWindowSize();
  const intl = useGetLocale();
  const handleFunctions: { [key: string]: () => void } = {
    handleLogout: async () => {
      await fetchBase("account/logout", {
        variables: {
          token,
        },
      });

      setToken("");
      setExpirationTime("");
    },
    onClose,
    feyorraUtilities: () => {
      history.push("feyorra-utilities");
      onClose();
    },
  };
  const customAdditionalTransform = useMemo(() => {
    if (isMobile) {
      return {
        x: -7,
        y: -60,
      };
    } else {
      return {
        x: -7,
        y: 33,
      };
    }
  }, [isMobile]);

  return (
    <Menu
      {...{
        isOpen,
        onClose,
        anchor,
        className: css.menu,
        customAdditionalTransform,
        isTriangle: true,
        classes: {
          triangle: css.triangle,
        },
      }}>
      <div className={css.container}>
        {menuItems.map((item) => {
          const {
            id,
            textLocaleId,
            to,
            search,
            classNameIndex,
            src,
            callbackIndex,
          } = item;

          if (width >= withoutFeyorraWidth && id === 3) {
            return null;
          }
          return (
            <MenuItem
              key={id}
              className={clsx(
                css.menuItem,
                classNameIndex && css[classNameIndex],
              )}
              search={search}
              onClick={handleFunctions[callbackIndex]}
              to={to}>
              <div className={css.icon}>
                <img src={src} alt="" />
              </div>
              {textLocaleId ? intl.formatMessage({ id: textLocaleId }) : ""}
            </MenuItem>
          );
        })}
      </div>
    </Menu>
  );
};

export default CustomMenu;
