import React from "react";

import bottomSnowURL from "assets/logos/christmas/bottomSnow.png";
import snowLineURL from "assets/logos/christmas/bottomSnow.svg";
import treeGiftsURL from "assets/logos/christmas/treeGifts.svg";
import treeURL from "assets/logos/christmas/tree.svg";

import css from "./styles.module.scss";

export const Christmas = () => {
  return (
    <div className={css.root}>
      <img src={bottomSnowURL} alt="snowBG" className={css.bottomSnow} />
      <div className={css.bottomSnowBlock} />

      <div className={css.treeWrapper}>
        <div className={css.leftLine}>
          <img src={snowLineURL} alt="snowLine" className={css.snowLineLeft} />
          <img src={treeURL} alt="tree" className={css.tree} />
        </div>

        <div className={css.rightLine}>
          <img src={treeGiftsURL} alt="treeGifts" className={css.treeGifts} />
          <img src={snowLineURL} alt="snowLine" className={css.snowLineRight} />
        </div>
      </div>
    </div>
  );
};
