import React from "react";

import { Cat } from "../Cat";
import { BoardContent } from "../BoardContent";

import css from "./style.module.scss";

interface Props {
  onButtonClick: () => void;
}

export const Board = ({ onButtonClick }: Props) => {
  return (
    <div className={css.boardContainer}>
      <Cat />
      <BoardContent onButtonClick={onButtonClick} />
    </div>
  );
};
