import React, { FC, useEffect, useState } from "react";
import SwipeableViews from "react-swipeable-views";
import clsx from "clsx";

import { useWindowSize } from "providers";
import { useFetch } from "hooks";

import { useNotification } from "providers/Notification";

import {
  RewardNotifications,
  InfoNotification,
  ChallengeNotification,
  TabsSwitcher,
} from "./components";
import css from "./style.module.scss";

import { Scrollbar } from "components/Scrollbar";

const swipeStyle = {
  marginRight: 10,
};

interface ViewedFetchType {
  notifications_count: number;
}

const testClnge = {
  challenge_type: "GET_MULTIPLIER",
  cur_win_users_count: "0",
  description:
    "Bet at least $0.10 at Crash and catch a minimum payout/multiplier of 100x to win 2 SOL! Ain't that lovely?! (Max Winners: 1)",
  end_time: "1646352000",
  game: "CRASH",
  id: "10",
  image_url: "https://cdn.pasino.com/user_uploads/98bbc7e8f8fb.gif",
  max_win_users_count: "1",
  min_bet: "0.10000000",
  min_win_multiplier: "100.00000000",
  reward_amount: "2.00000000",
  reward_coin: "SOL",
  title: "Hit 100x at Crash",
  total_profit_amount: "0.00000000",
  wager_amount: "0.00000000",
};

const NotificationWrapper: FC = () => {
  const [openedIndex, setOpenedIndex] = useState<number | string>(0);
  const { notification, missed, setMissed, setMissedChallenges } =
    useNotification();
  const { width } = useWindowSize();

  const [setNotificationsViewed, { data: notificationsViewedData }] =
    useFetch<ViewedFetchType>("notification/view-all-notifications");

  const tabChange = (newValue: number | string) => {
    setOpenedIndex(newValue);
  };

  useEffect(() => {
    if (notification.isOpen) {
      switch (openedIndex) {
        case 0:
          if (!!missed) {
            setNotificationsViewed();
          }
          break;
        case 1:
          //fetch reward if needed
          break;
        case 2:
          setMissedChallenges(false);
          const revisedNotifications = notification.challenges.map(
            ({ id }) => id,
          );
          localStorage.setItem(
            "revisedPasinoChallenges",
            JSON.stringify(revisedNotifications),
          );
          break;

        default:
          break;
      }
    }
  }, [notification.isOpen, missed, openedIndex]);

  useEffect(() => {
    if (notificationsViewedData) {
      setMissed(notificationsViewedData.notifications_count);
    }
  }, [notificationsViewedData]);

  return (
    <div
      className={clsx(css.root, {
        [css.mobileRoot]: width <= 600,
      })}>
      <TabsSwitcher value={openedIndex} tabChange={tabChange} />
      <Scrollbar>
        <SwipeableViews
          style={swipeStyle}
          disabled
          disableLazyLoading={false}
          index={Number(openedIndex)}>
          <InfoNotification />
          <RewardNotifications />
          <ChallengeNotification />
        </SwipeableViews>
      </Scrollbar>
    </div>
  );
};

export default NotificationWrapper;
