import React, { useEffect, useLayoutEffect, useRef, FC } from "react";
import { useLocation } from "react-router";
import clsx from "clsx";

import { useChat, useWindowSize } from "providers";
import { whenDocumentReady } from "utils";
import { Chat } from "components";

import BalanceAnimationProvider from "providers/BalanceAnimation";

import { MobileNavigation } from "./components";
import MobileNotifications from "./components/MobileNotifications";
import OfflineAlert from "./components/OfflineAlert";
import Content from "./components/Content";
import AppBar from "./components/AppBar";

import css from "./style.module.scss";
import "./general.css";

const mobileScreen = parseInt(css.mobileScreen);

const Page: FC = ({ children }) => {
  const { width } = useWindowSize();

  const { pathname: path } = useLocation();
  const { isHide } = useChat();

  const shadowTop = useRef<HTMLDivElement>(null);
  const shadowBottom = useRef<HTMLDivElement>(null);

  useLayoutEffect(() => {
    const handleUpdate = (event: any) => {
      if (!event.target || document.readyState !== "complete") {
        return;
      }

      const { scrollTop, scrollHeight, clientHeight } = event.target
        .documentElement as any;

      const shadowTopOpacity = 0.05 * Math.min(scrollTop, 20);
      const bottomScrollTop = scrollHeight - clientHeight;
      const shadowBottomOpacity =
        (1 / 20) *
        (bottomScrollTop - Math.max(scrollTop, bottomScrollTop - 20));

      if (shadowTop.current?.style) {
        shadowTop.current.style.opacity = `${shadowTopOpacity}`;
      }

      if (shadowBottom.current?.style) {
        shadowBottom.current.style.opacity = `${shadowBottomOpacity}`;
      }
    };

    handleUpdate({ target: document });
    window.addEventListener("scroll", handleUpdate);

    return () => {
      window.removeEventListener("scroll", handleUpdate);
    };
  }, []);

  useEffect(() => {
    whenDocumentReady(() => {
      window.scrollTo({
        top: 0,
      });
    });
  }, [path]);

  return (
    <>
      <OfflineAlert />

      <div ref={shadowTop} className={css.shadowTop} />
      <div ref={shadowBottom} className={css.shadowBottom} />

      <MobileNotifications />
      <div className={css.root}>
        <BalanceAnimationProvider>
          <Content>
            <AppBar />
            <div className={css.wrapper}>
              <div className={css.gridWrapper}>
                <div className={css.gridInnerWrapper}>
                  <div
                    className={clsx(css.gridContentWrapper, {
                      [css.gridContentWrapperWithoutChat]: isHide,
                    })}>
                    <div className={css.gridContentInnerWrapper}>
                      {children}
                    </div>
                  </div>
                  <Chat type={width > 600 ? "desktop" : "mobile"} />
                </div>
              </div>
            </div>
            {width <= mobileScreen && <MobileNavigation />}
          </Content>
        </BalanceAnimationProvider>
      </div>
    </>
  );
};

export default Page;
