import React, { useRef } from "react";
import { Button } from "components";

import { WalletIcon } from "./assets";
import { CoinContainer } from "./components";
import { useModalLocation } from "hooks";

import css from "./style.module.scss";

export const WalletBar = () => {
  const rootRef = useRef<HTMLDivElement>(null);
  const { pushHistoryTo } = useModalLocation("wallet");

  const handleIconClick = () => () => {
    pushHistoryTo({});
  };

  return (
    <div ref={rootRef} className={css.walletContainer}>
      <CoinContainer anchor={rootRef.current} withCoinName={false} />

      <Button
        className={css.walletButton}
        classes={{ label: css.btnLabel }}
        onClick={handleIconClick()}>
        <WalletIcon className={css.walletIcon} />
      </Button>
    </div>
  );
};
