import React from "react";
import { useHistory } from "react-router-dom";

import ProgressFallback from "components/fallbacks/Progress";
import { modalsLoaderColor } from "sass/styleVariables";

import css from "./style.module.scss";

const ModalsFallback = () => {
  const history = useHistory();

  const onClose = () => {
    history.push({
      pathname: history.location.pathname,
      search: "",
    });
  };

  return (
    <div className={css.root} onClick={onClose}>
      <ProgressFallback
        size={150}
        circularWidth={8}
        color={modalsLoaderColor}
      />
    </div>
  );
};

export default ModalsFallback;
