import React, { useContext, forwardRef, ReactElement } from "react";
import { TabContext } from "../TabsWrapper";
import css from "./style.module.scss";

export interface Props {
  value?: number | string;
  children: ReactElement;
}

export const Tab = forwardRef<HTMLDivElement, Props>(
  ({ children, value, ...props }, ref) => {
    const { tabOnChange, value: selectedTab } = useContext(TabContext);

    return (
      <div
        className={`${css.root} ${
          value === selectedTab ? `${css.selected}` : null
        }`}
        ref={ref}
        onClick={(event) => {
          if (value !== undefined) {
            tabOnChange(event as any, value);
          }
        }}>
        {children}
      </div>
    );
  },
);
