import React from "react";

import CatImg from "../../assets/cat-without-paw.webp";
import PawOfCat from "../../assets/paw-of-cat.webp";
import css from "./style.module.scss";

export const Cat = () => {
  return (
    <div className={css.catContainer}>
      <img src={PawOfCat} alt="" className={css.paw} />
      <img src={CatImg} alt="" className={css.cat} />
    </div>
  );
};
