import React, { useCallback, FC } from "react";
import { TabsWrapper } from "./components/TabsWrapper";
import { Tab } from "./components/Tab";

import BellIcon from "assets/icons/new-bell.png";
import MoneyBagIcon from "assets/icons/new-moneybag.png";
import BullSeyeIcon from "assets/icons/new-bullseye.png";
import css from "./style.module.scss";

interface Props {
  value: number | string;
  tabChange: (newValue: number | string) => void;
}

export const TabsSwitcher: FC<Props> = ({ value, tabChange }) => {
  const handleChange = (ev: any, value: number | string) => {
    tabChange(value);
  };
  return (
    <div className={css.root}>
      <TabsWrapper value={value} onChange={handleChange}>
        <Tab>
          <img src={BellIcon} alt="icon" />
        </Tab>
        <Tab>
          <img src={MoneyBagIcon} alt="money bag" />
        </Tab>
        <Tab>
          <img src={BullSeyeIcon} alt="bullseye" />
        </Tab>
      </TabsWrapper>
    </div>
  );
};
