import React from "react";

import { useGetLocale } from "providers";
import { useNotification } from "providers/Notification";

import Notification from "./Notification";
import css from "./style.module.scss";

export const ChallengeNotification = () => {
  const intl = useGetLocale();

  const { notification } = useNotification();

  const { challenges } = notification;

  return (
    <div className={css.root}>
      {challenges.length ? (
        challenges.map((props) => {
          return <Notification key={props.id} {...props} />;
        })
      ) : (
        <span className={css.text}>
          {intl.formatMessage({ id: "notification.content.no_challenges" })}
        </span>
      )}
    </div>
  );
};
