import React, {
  FC,
  Suspense,
  useCallback,
  useEffect,
  useMemo,
  useState,
} from "react";
import { Route, useHistory } from "react-router-dom";

import { useLocalStorage } from "hooks";

import { ModalParams, modalRoutes } from "config/routes";
import { useHotKeys } from "components/HotKeys/Provider";
import { getModalsProps } from "./getModalsProps";
import { FairnessGameName } from "providers/Seed/Provider";
import ModalsFallback from "./fallback";
import { whenDocumentReady } from "utils";

interface Props {
  isOpen: boolean;
  background: any;
  location: any;
  gameName: FairnessGameName | null;
}

const Modals: FC<Props> = ({ isOpen, background, location, gameName }) => {
  const [isLoaded, setIsLoaded] = useState(false);
  const [token] = useLocalStorage("token");
  const { push } = useHistory();
  const { setIsHotKeysPaused } = useHotKeys();

  const redirectCallback = useCallback(
    (isOpen) => {
      if (!token && isOpen) {
        push({ ...background });
      }
    },
    [token, location, background],
  );

  useEffect(() => {
    if (isOpen) {
      setIsHotKeysPaused(true);
    } else {
      setIsHotKeysPaused(false);
    }

    return () => {
      setIsHotKeysPaused(false);
    };
  }, [isOpen]);
  useEffect(() => {
    whenDocumentReady(() => {
      setTimeout(() => {
        setIsLoaded(true);
      }, 500);
    });
  }, []);
  if (!isLoaded) return null;
  return (
    <>
      {modalRoutes.map(({ Component, ...other }, i) => {
        const { modalSearch, path } = other;

        let params = {};
        let pathValue = [];
        if (path) {
          pathValue.push(path);
        }
        if (modalSearch) {
          params = ModalParams[modalSearch?.value];
          pathValue[0] = `/${modalSearch?.name}/${modalSearch?.value}`;
        }
        if (Object.keys(params).length) {
          pathValue[1] =
            `/${modalSearch?.name}/${modalSearch?.value}/:` +
            Object.values(params).join("/:");
        }
        const props = getModalsProps({
          ...other,
          token,
          location,
          redirectCallback,
        });
        return (
          <Route
            path={pathValue}
            exact
            children={
              <Suspense fallback={ModalsFallback}>
                {props.isOpen ? (
                  <Component
                    gameName={gameName}
                    key={`@customModals/${i}`}
                    {...props}
                  />
                ) : null}
              </Suspense>
            }></Route>
        );
      })}
    </>
  );
};

export default Modals;
