import React, { FunctionComponent, useCallback, useState } from "react";
import { getAuth, track } from "utils";

import Context, { Auth, OptionalAuth } from "./Context";

const persistKey = "auth";

const Provider: FunctionComponent = ({ children }) => {
  const [auth, setAuth] = useState<Auth>(getAuth());

  const handleChange = useCallback(
    (props: OptionalAuth) => {
      try {
        setAuth((currentAuth) => {
          window.localStorage.setItem(
            persistKey,
            JSON.stringify({ ...currentAuth, ...props }),
          );

          return { ...currentAuth, ...props };
        });
      } catch (error) {
        const message = `
${error}
Auth > Provider > handleChange
Props: ${JSON.stringify(props)}
Auth: ${JSON.stringify(auth)}`;

        try {
          track()?.errors(message);
        } catch {
          console.log("🚀 trackError", error, auth, props);
        }
      }
    },
    [setAuth],
  );

  return (
    <Context.Provider value={{ auth: auth, setAuth: handleChange }}>
      {children}
    </Context.Provider>
  );
};

export default Provider;
