import React, { Suspense, useEffect, useMemo, useState } from "react";
import { Route, Switch, useLocation } from "react-router-dom";
import { Helmet } from "react-helmet";

import { createSuspenseComponent, track } from "utils";

import ThemeProvider from "providers/WhiteTheme";
import LocaleProvider from "providers/Locale";
import { routesWithoutPageWrapper } from "config/routes";
import { Providers } from "containers/Providers";
import NotifyBar from "components/NotifyBar";
import Layout from "containers/Layout";
import Modals from "containers/Layout/Modals";
import ModalsFallback from "containers/Layout/Modals/fallback";
import { FairnessGameName, fairnessGameNames } from "providers/Seed/Provider";

import { createPolyfills } from "polyfills";
import "./style.scss";

interface AppComponentProps {
  hasError: boolean;
}

const lsTest = () => {
  var test = "pasino";
  try {
    localStorage.setItem(test, test);
    localStorage.removeItem(test);
    createPolyfills();
    return true;
  } catch (e) {
    track()?.errors(e);
    return false;
  }
};

export const ErrorScreen = createSuspenseComponent(import("./ErrorScreen"));
export const RequestSettingData = createSuspenseComponent(
  import("./RequestSettingData"),
);
const defaultLocation = {
  pathname: "/",
  search: "",
  hash: "",
  state: {
    hash: "",
    pathname: "/",
    search: "",
    state: undefined,
  },
};

const AppComponent = ({ hasError }: AppComponentProps) => {
  const location = useLocation<{ background?: any }>();

  const { background, isModal, gameName } = useMemo(() => {
    const paths = location.pathname.split("/").filter((val: string) => val);
    const isModal = paths.includes("page");
    let background =
      location.state && location.state.background && isModal
        ? location.state.background
        : defaultLocation;

    let backgroundPaths = [];
    if (background) {
      background = isModal ? background : location;
      backgroundPaths = background.pathname
        .split("/")
        .filter((val: string) => val);
    }

    let gameName: FairnessGameName | null = null;
    for (let game in fairnessGameNames) {
      let path = isModal ? backgroundPaths[0] : paths[0];

      if (fairnessGameNames[game as FairnessGameName] === path) {
        gameName = game as FairnessGameName;
      }
    }
    return { background, isModal, gameName };
  }, [location]);

  return (
    <>
      {location?.pathname === "/" && (
        <Helmet defer={false}>
          {/* <!-- Primary Meta Tags --> */}
          <title>Pasino - Crypto Gaming Casino</title>
          <meta name="title" content="Pasino - Crypto Gaming Casino" />
          <meta
            name="description"
            content="Unparalleled crypto gaming from the makers of FaucetPay! Provably Fair
Gambling. Instant deposits and withdrawals"
          />
          , Suspense
          {/* <!-- Open Graph / Facebook --> */}
          <meta property="og:type" content="website" />
          <meta property="og:url" content="https://pasino.com/" />
          <meta property="og:title" content="Pasino - Crypto Gaming Casino" />
          <meta
            property="og:description"
            content="Unparalleled crypto gaming from the makers of FaucetPay! Provably Fair
Gambling. Instant deposits and withdrawals"
          />
          <meta property="og:image" content="" />
          {/* <!-- Twitter --> */}
          <meta property="twitter:card" content="summary_large_image" />
          <meta property="twitter:url" content="https://pasino.com/" />
          <meta
            property="twitter:title"
            content="Pasino - Crypto Gaming Casino"
          />
          <meta
            property="twitter:description"
            content="Unparalleled crypto gaming from the makers of FaucetPay! Provably
Fair Gambling. Instant deposits and withdrawals"
          />
          <meta property="twitter:image" content="" />
        </Helmet>
      )}
      {!lsTest() ? (
        <RequestSettingData />
      ) : (
        <LocaleProvider>
          <ThemeProvider>
            <NotifyBar />
            {hasError ? (
              <ErrorScreen />
            ) : (
              <Providers>
                <Switch location={background}>
                  {routesWithoutPageWrapper.map((Route, i) => {
                    return React.cloneElement(Route, {
                      key: `@customRoutesWithoutPageWrapper/${i}`,
                    });
                  })}
                  <Route children={<Layout appLocation={background} />} />
                </Switch>
                <Switch>
                  <Suspense fallback={<ModalsFallback />}>
                    <Modals
                      gameName={gameName}
                      isOpen={isModal}
                      background={background}
                      location={location}
                    />
                  </Suspense>
                </Switch>
              </Providers>
            )}
          </ThemeProvider>
        </LocaleProvider>
      )}
    </>
  );
};

export default AppComponent;
