import React from "react";

import { Typography } from "components";

import css from "./style.module.scss";

export const Title = () => {
  return (
    <div className={css.titleWrapper}>
      <div className={css.dailyWrapper}>
        <Typography className={css.dailyFirstLayer}>
          DAI
          <span className={css.letterL}>L</span>Y
        </Typography>
        <Typography className={css.dailySecondLayer}>
          DAI
          <span className={css.letterL}>L</span>Y
        </Typography>
        <Typography className={css.dailyThirdLayer}>
          DAI
          <span className={css.letterL}>L</span>Y
        </Typography>
      </div>
      <div className={css.winWrapper}>
        <Typography className={css.winFirstLayer}>WIN</Typography>
        <Typography className={css.winSecondLayer}>WIN</Typography>
        <Typography className={css.winThirdLayer}>WIN</Typography>
      </div>
    </div>
  );
};
