//@ts-nocheck
import { track } from "utils";
import { objectValuesPolifill } from "./objectValues";
// import scrollPolyfill from "scroll-polyfill";

export const createPolyfills = async () => {
  try {
    if (!window.ResizeObserver) {
      class Empty {
        observe() {}
        unobserve() {}
      }

      window.ResizeObserver = Empty;

      const { ResizeObserver } = await import("resize-observer");

      window.ResizeObserver = ResizeObserver;

      console.log("create resize-observer polyfill");
    }
    if (!Object.values) {
      Object.values = objectValuesPolifill;

      console.log("create Object.values polyfill");
    }

    if (!window.scroll || !window.scrollTo || !Element.prototype.scrollTo) {
      window.scrollTo = () => {};
      window.scroll = () => {};
      Element.prototype.scrollTo = () => {};

      const { default: scrollPolyfill } = await import("scroll-polyfill");

      scrollPolyfill({ force: true });

      console.log("create scrollTo polyfill");
    }
  } catch (error) {
    track()?.errors(error);
    console.error("error on create polyfills: ", error);
  }
};
