import { notifyBarManager } from "components";
import { useFetch, useLocalStorage } from "hooks";
import { useGetLocale } from "providers/Locale";
import React, { FunctionComponent, useEffect, useRef, useState } from "react";
import { createIntervals, track } from "utils";

import Context from "./Context";
import { NotificationAudio, NotificationData, NotificationType } from "./types";

const { clearIntervals, pushInterval } = createIntervals();

let interimMissed = 0,
  playSound: keyof NotificationAudio | null = null,
  isInteracted = false;

const Provider: FunctionComponent = ({ children }) => {
  const intl = useGetLocale();
  const [token] = useLocalStorage("token");
  const [missed, setMissed] = useState(0);
  const [missedChallenges, setMissedChallenges] = useState(false);
  const [notification, setNotification] = useState<NotificationData>({
    isOpen: false,
    notifications: [],
    rewards: [],
    challenges: [],
  });

  const audio = useRef<NotificationAudio>({
    collectBonus: new Audio("/assets/general/collect_bonus.mp3"), //error on midori
    levelUp: new Audio("/assets/general/level_up.mp3"), //error on midori
    challenges: new Audio("/assets/general/new_note.mp3"), //error on midori
  });

  const [getNotification, { data, error }] = useFetch<NotificationType>(
    "notification/get-all",
  );

  const playAudio = async (
    whichSound: keyof NotificationAudio = "collectBonus",
  ) => {
    try {
      const sound = audio.current ? audio.current[whichSound] : null;
      let canPlay = true;

      // checking if another melody is playing:
      Object.values(audio.current as NotificationAudio).forEach(
        (anotherSound) => {
          if (anotherSound !== sound && !anotherSound.paused) {
            canPlay = false;
          }
        },
      );

      if (canPlay) {
        await sound?.play();
      }
    } catch (error) {
      track()?.errors(error);

      console.error("audio play error", error);
    }
  };

  const playListener = () => {
    if (playSound) playAudio(playSound);
    document.body.removeEventListener("click", playListener);
  };

  useEffect(() => {
    if (!data && notification.isOpen && token) {
      getNotification({
        variables: { token },
      });
    }
  }, [token, notification.isOpen, data]);

  useEffect(() => {
    if (token) {
      getNotification({
        variables: { token },
      });

      const minutesInterval = 1;
      pushInterval(() => {
        getNotification({
          variables: { token },
        });
      }, 60000 * minutesInterval);
    } else {
      clearIntervals();
    }

    return () => {
      clearIntervals();
    };
  }, [token]);

  useEffect(() => {
    if (data) {
      const { challenges, notifications, rewards, notifications_count } = data;
      playSound = null;

      if (notifications_count && notifications_count !== missed) {
        const isNotify = notifications.find(({ viewed }) => viewed === "0"); //search unreviewed notification
        if (isNotify) {
          playSound = "collectBonus";
        }

        const isReward = rewards.find(({ viewed }) => viewed === "0"); //search unreviewed rewards

        if (isReward) {
          const rewardType =
            isReward.title.indexOf("Level") !== -1 ? "levelUp" : "collectBonus";
          playSound = rewardType;
        }
      }

      if (notification.challenges.length < challenges.length) {
        const revisedChallenges = JSON.parse(
          localStorage.getItem("revisedPasinoChallenges") || "[]",
        ) as string[];

        challenges.forEach(({ id }) => {
          if (!revisedChallenges.includes(id)) {
            setMissedChallenges(true);
            playSound = "challenges";
          }
        });
      }

      if (playSound) {
        if (isInteracted) {
          playAudio(playSound);
        } else {
          isInteracted = true;
          document.body.addEventListener("click", playListener);
        }
      }

      if (!challenges.length) {
        localStorage.removeItem("revisedPasinoChallenges");
      }

      setNotification({
        ...notification,
        notifications: notifications,
        rewards: rewards,
        challenges: challenges,
      });

      setMissed(notifications_count);
      interimMissed = notifications_count;
    } else if (error) {
      setNotification({
        ...notification,
        isOpen: false,
        notifications: [],
        rewards: [],
        challenges: [],
      });
      setMissed(0);
    }

    return () => {
      document.body.removeEventListener("click", playListener);
    };
  }, [data]);

  useEffect(() => {
    audio.current.levelUp.volume = 0.5;
    audio.current.collectBonus.volume = 0.5;
  }, []);

  useEffect(() => {
    if (!token && notification.isOpen) {
      notifyBarManager.error({
        timeOut: 4000,
        message: intl.formatMessage({ id: "crashInterface.messages.noLogin" }),
      });

      setNotification((old) => ({ ...old, isOpen: false }));
    }
  }, [notification.isOpen, token]);

  return (
    <Context.Provider
      value={{
        notification,
        setNotification,
        audio,
        playAudio,
        missed,
        setMissed,
        missedChallenges,
        setMissedChallenges,
      }}>
      {children}
    </Context.Provider>
  );
};

export default Provider;
