import React, { Suspense, useEffect, useState } from "react";

import { Icon, Banner, Logo, LoginButton } from "components";
import { useLocalStorage } from "hooks";
import { useWhiteTheme, useWindowSize } from "providers";

import { useMenu } from "providers/Menu";
import { useUser } from "providers/User";
import { DailyWheel } from "pages/ModalPages/pages/DailyWheel";
import { DailyWin } from "pages/ModalPages/pages/DailyWin";
import Loader from "config/LazyComponentLoader";

import { BonusBannerText, Ringer, WalletBar } from "./components";
import UserMore from "./UserMore";
import css from "./style.module.scss";
import clsx from "clsx";

const EventLogo = React.lazy(() => Loader(import("./components/EventLogo")));

const loginStyle = { marginLeft: 8 };

const AppBarComponent = () => {
  const { isMobile } = useWindowSize();
  const { activeEvent } = useWhiteTheme();

  const [token] = useLocalStorage("token");
  const { user } = useUser();

  const [isOpenWheel, setIsOpenWheel] = useState<boolean>(false);
  const [isOpenDailyWin, setIsOpenDailyWin] = useState<boolean>(false);

  const { isOpen, setOpen } = useMenu();

  const handleDrawerMenuClick = () => {
    setOpen(!isOpen);
  };

  useEffect(() => {
    if (!user) {
      return;
    }
    setIsOpenWheel(user.eligible_daily_wheel);
  }, [user?.eligible_daily_wheel]);

  useEffect(() => {
    if (token) {
      return;
    }
    setIsOpenDailyWin(true);
  }, [token]);

  const closeWheel = () => {
    setIsOpenWheel(false);
  };

  return (
    <header className={css.appBar}>
      <div className={css.background} />
      <div className={css.toolbar}>
        <Icon
          variant="menu"
          className={css.menuButton}
          aria-label="open drawer"
          onClick={handleDrawerMenuClick}
          fontSize={32}
        />
        {activeEvent ? (
          <Suspense fallback={null}>
            <EventLogo eventName={activeEvent} />
          </Suspense>
        ) : (
          <Logo
            className={css.pasinoLogo}
            logoVariant="footerPasino"
            to={{ pathname: "/" }}
          />
        )}

        <div className={css.grow} />

        <Banner
          buttonVariant="feyorraUtilities"
          to={{ pathname: "/feyorra-utilities" }}
          className={clsx(css.banner, css.feyorra)}
        />
        <Banner
          buttonVariant="winBonuses"
          to={{ pathname: "/bonus" }}
          className={css.banner}
          BannerText={BonusBannerText}
        />
        <Banner
          buttonVariant="inviteFriends"
          to={{ pathname: "/affiliate" }}
          className={css.banner}
        />

        {token ? (
          <>
            <WalletBar />
            {!isMobile ? (
              <>
                <Ringer />
                <UserMore />
              </>
            ) : null}
          </>
        ) : (
          <>
            <LoginButton
              withBorder
              style={loginStyle}
              className={css.loginBtn}
            />
          </>
        )}
      </div>
      {isOpenWheel && <DailyWheel isOpen={isOpenWheel} onClose={closeWheel} />}
      {isOpenDailyWin && (
        <DailyWin
          isOpen={isOpenDailyWin}
          onClose={() => setIsOpenDailyWin(false)}
        />
      )}
    </header>
  );
};

export default AppBarComponent;
