import React from "react";

import { Dialog } from "containers/PopUps";

import { Wheel } from "../DailyWheel/components/Wheel";
import { Board } from "./components/Board";
import { wheelItems } from "./config";
import css from "./style.module.scss";

interface Props {
  isOpen: boolean;
  onClose: () => void;
}

export const DailyWin = ({ isOpen, onClose }: Props) => {
  return (
    <Dialog
      onClose={onClose}
      isOpen={isOpen}
      paper={{
        className: css.paper,
      }}>
      <div className={css.contentWrapper}>
        <div className={css.wheelsWrapper}>
          <Wheel items={wheelItems} rotate={null} isDailyWin />
        </div>
        <div className={css.boardWrapper}>
          <Board onButtonClick={onClose} />
        </div>
      </div>
    </Dialog>
  );
};
