import React from "react";
import { useGetLocale } from "providers";

import { NotificationType } from "components";

import Notification from "components/NotifyBar/Notification";
import { useOnline } from "providers/Online";
import css from "./style.module.scss";

const OfflineAlert = () => {
  const intl = useGetLocale();

  const isOnline = useOnline();

  if (isOnline) {
    return null;
  }

  return (
    <div className={css.root}>
      <Notification
        className={css.offline}
        message={intl.formatMessage({ id: "offline" })}
        type={NotificationType.ERROR}
        removeNotification={false}
      />
    </div>
  );
};

export default OfflineAlert;
