import React, { useState, useRef, useMemo } from "react";
import clsx from "clsx";

import {
  Icon,
  Typography,
  ClickAwayListener,
  Popper,
  MenuItem,
  Divider,
  SwitchBox,
} from "components";

import { ReactComponent as ArrowIcon } from "assets/icons/arrow.svg";
import css from "./style.module.scss";
import { SelectOption, SelectProps } from "../WalletBar/type";

interface labelProps {
  curValue: string;
  options: SelectOption[];
  isOpen: boolean;
  isUSD: boolean;
  css: any;
}

const switchBoxClasses = {
  root: css.switchBoxRoot,
  switch: {
    track: css.switchThumb,
    toggle: css.switchToggle,
    root: css.switchRoot,
  },
  text: clsx(css.switchText),
};

