export const CoinsSelect = ({
  id,
  value: curValue,
  handleChange,
  options,
  showInUSD,
  setShowInUSD,
}: SelectProps) => {
  const input = useRef<null | HTMLDivElement>(null);
  const [anchorEl, setAnchorEl] = useState<HTMLDivElement | null>(null);
  const isOpen = Boolean(anchorEl);

  const totalAmount = useMemo(() => {
    return (
      Math.floor(
        options.reduce((acm, { label, price }) => {
          return acm + Number(label) * Number(price || 0);
        }, 0) * 100,
      ) / 100
    );
  }, [options]);

  const onInputClick = (e: any) => {
    e.stopPropagation();
    if (isOpen) {
      closeSelect();
    } else {
      openSelect();
    }
  };

  const closeSelect = () => {
    setAnchorEl(null);
  };
  const openSelect = () => {
    setAnchorEl(input.current);
  };

  const onAwayClick = () => {
    if (isOpen) {
      closeSelect();
    }
  };

  const handleValueChange = (value: string) => {
    handleChange(value);
    closeSelect();
  };

  const onChangeSwitch = () => {
    setShowInUSD(!showInUSD);
  };

  return (
    <div className={css.root}>
      {/* <div ref={input} onClick={onInputClick} id={id}></div> */}
      <div>
        <ClickAwayListener onClickAway={onAwayClick}>
          <Popper
            recalculate
            isOpen={isOpen}
            anchor={anchorEl}
            position="bottom"
            className={css.popper}
            fixed>
            <div className={css.container}>
              <div className={css.header}>
                <span>Wallet</span>
                <Icon
                  variant="close"
                  fontSize={12}
                  onClick={closeSelect}
                  className={css.close}
                />
              </div>
              <div className={css.itemsContainer}>
                {options?.map(({ value, label, icon, annex, price, name }) => [
                  <MenuItem
                    key={value}
                    waveColor="white"
                    asButton
                    selected={value === curValue}
                    classes={{ selected: css.selectedItem }}
                    onClick={() => handleValueChange(value)}
                    className={clsx(css.fontFamily, css.menuItem)}>
                    {icon && (
                      <img className={css.icon} src={icon} alt={value} />
                    )}
                    <span className={css.coinName}>{name}</span>
                    <div className={css.grow} />
                    {showInUSD ? (
                      <div className={css.moneyBox}>
                        <span>
                          ${(Number(label) * Number(price)).toFixed(8)}
                        </span>
                        <span className={css.coinValue}>{label}</span>
                      </div>
                    ) : (
                      <span className={css.coinValue}>{label}</span>
                    )}

                    {annex && (
                      <Typography className={css.menuItemAnnex}>
                        {annex}
                      </Typography>
                    )}
                  </MenuItem>,
                  <Divider key={value + "d"} className={css.divider} />,
                ])}
              </div>
              <div className={css.controlBox}>
                <div className={css.switchBox}>
                  <span>View in USD</span>

                  <SwitchBox
                    isChecked={showInUSD}
                    switchHandleChange={onChangeSwitch}
                    text={{
                      off: "OFF",
                      on: "ON",
                    }}
                    classes={switchBoxClasses}
                  />
                </div>
                <div className={css.amountBox}>
                  <span>Total amount</span>
                  <span className={css.amountValue}>{totalAmount} USD</span>
                </div>
              </div>
            </div>
          </Popper>
        </ClickAwayListener>
      </div>
    </div>
  );
};

export default CoinsSelect;
