import React, { useState, FunctionComponent, useEffect } from "react";
import clsx from "clsx";

import { Icon, notifyBarManager } from "components";
import { coinsManager, useCoins, useFetch } from "hooks";

import { useWindowSize } from "providers";
import { Dialog } from "containers/PopUps";

import { WheelContainer } from "./components/WheelContainer";
import parts from "./assets/parts.gif";
import { Timer } from "./timerCanvas";
import css from "./style.module.scss";
import { track } from "utils";

interface Props {
  isOpen: boolean;
  onClose: () => void;
}

const initialState = {
  showTimer: false,
  showReward: false,
};
interface State {
  showTimer: boolean;
  showReward: boolean;
}
export interface Reward {
  id: string;
  reward_amount: string;
  reward_coin: string;
}
interface FetchRewards {
  message: string;
  success: boolean;
  rewards: Reward[];
}

interface SpinFetch {
  message: string;
  success: boolean;
  next_spin_time: string;
  result: string;
}
interface SpinResult {
  next_spin_time: number | null;
  result: number | null;
}

export const DailyWheel: FunctionComponent<Props> = ({ isOpen, onClose }) => {
  const { isMobile: isSm } = useWindowSize();

  const { coinsList } = useCoins("coinsList");

  const [animationStatus, setAnimationStatus] = useState<State>(initialState);
  const [fetch, rewardsFetch] = useFetch<FetchRewards>("dailywheel/rewards");
  const [fetchSpin, { data, error, loading, called }] =
    useFetch<SpinFetch>("dailywheel/spin");
  const [rewards, setRewards] = useState<Reward[] | null>(null);
  const [spinResult, setSpinResult] = useState<SpinResult>({
    next_spin_time: null,
    result: null,
  });
  const [canClose, setCanClose] = useState(false);

  useEffect(() => {
    fetch();
  }, [fetch]);

  useEffect(() => {
    if (!rewardsFetch.data) {
      return;
    }
    setRewards(rewardsFetch.data.rewards);
  }, [rewardsFetch.data]);

  useEffect(() => {
    if (!data) {
      return;
    }
    const index = rewards!.findIndex((rew) => rew.id === data.result);
    if (index >= 0) {
      setSpinResult({
        result: index,
        next_spin_time: performance.now() + 86400000,
      });
    }
    setTimeout(() => handleAnimationFinish(index), 6000);
  }, [data]);

  useEffect(() => {
    if (error) {
      notifyBarManager.error({
        message: error,
        timeOut: 4000,
      });
    }
  }, [error]);

  const handleAnimationFinish = (index: number) => {
    setAnimationStatus({
      ...animationStatus,
      showTimer: true,
      showReward: true,
    });
    const coin = coinsList.find(
      (item) => item.coin === rewards![index].reward_coin,
    );

    if (!coin) {
      return;
    }
    const balance =
      Number(coin.balance) + Number(rewards![index].reward_amount);
    coinsManager.coinValueUpdate(balance.toFixed(8), coin.coin);
  };

  const handleStartAnimation = () => {
    if (spinResult.result !== null || called || loading) {
      return;
    }
    const audioPromise = new Audio(
      "assets/audio/SoundEffects/daily-wheel/result.mp3",
    ).play();
    audioPromise.then().catch((err) => {
      track()?.errors(err);

      console.log(err);
    });
    fetchSpin();
  };

  const handleRewardClose = () => {
    setAnimationStatus({
      ...animationStatus,
      showReward: false,
    });
  };

  const disableOnClose = !!(
    (spinResult.result || spinResult.result === 0) &&
    !canClose
  );

  const onCloseHandler = () => {
    if (disableOnClose) {
      return;
    }

    onClose();
  };

  useEffect(() => {
    if (animationStatus.showReward) {
      setCanClose(true);
    }
  }, [animationStatus.showReward]);

  return (
    <Dialog
      isOpen={isOpen}
      fullScreen={isSm}
      paper={{
        className: css.paper,
      }}
      disableOnClose={disableOnClose}
      onClose={onCloseHandler}>
      <div className={css.root}>
        <Icon
          variant="close"
          fontSize={12}
          onClick={onCloseHandler}
          className={css.closeIcon}
        />
        <div className={css.wheelsContainer}>
          {rewards && isOpen ? (
            <WheelContainer items={rewards} position={spinResult.result} />
          ) : null}
        </div>
        <div className={css.buttonContainer}>
          <button
            className={clsx(css.button, {
              [css.hideButton]:
                animationStatus.showReward || animationStatus.showTimer,
            })}
            onClick={handleStartAnimation}
            disabled={spinResult.result !== null}>
            <div>Spin Now</div>
          </button>

          <Timer
            start={animationStatus.showTimer}
            next_spin_time={spinResult.next_spin_time}
          />
        </div>
      </div>
      <div
        onClick={handleRewardClose}
        className={clsx(css.rewardContainer, {
          [css.rewardContainerVisible]: animationStatus.showReward,
        })}>
        <div
          className={clsx(css.rewardBorder, {
            [css.rewardVisible]: animationStatus.showReward,
          })}>
          {spinResult.result !== null && rewards ? (
            <div className={css.reward}>
              <span className={css.rewardHeader}>Reward</span>
              <div className={css.rewardVal}>
                <span>{rewards[spinResult.result].reward_amount}</span>

                <img
                  src={`https://cdn.pasino.com/coins/${rewards[
                    spinResult.result
                  ].reward_coin.toLowerCase()}.png`}
                  alt="wheel"
                />
              </div>
            </div>
          ) : null}
        </div>
        {animationStatus.showReward ? (
          <img src={parts} className={css.img} />
        ) : null}
      </div>
    </Dialog>
  );
};

export default React.memo(DailyWheel);
