import React, { FC, FunctionComponent, SVGProps, useRef } from "react";
import clsx from "clsx";

import { Button, ButtonProps } from "components";
import css from "./style.module.scss";
import { MoreMenu } from "../Menu";

interface Props extends Omit<ButtonProps, "icon"> {
  icon: FunctionComponent<SVGProps<SVGSVGElement>>;
  logo?: boolean;
  value?: string | number;
  moreMenu?: {
    isOpen: boolean;
    handleClose: () => void;
  };
}

export const MenuButton: FC<Props> = ({
  icon: Icon,
  logo: isLogo,
  value,
  moreMenu,
  ...props
}) => {
  const button = useRef<HTMLButtonElement>(null);
  return (
    <>
      <Button
        ref={button}
        className={clsx(css.root, isLogo && css.logo)}
        {...props}>
        {!!value && <span className={css.valueItem}>{value}</span>}
        <Icon />
      </Button>

      {moreMenu && (
        <MoreMenu
          isOpen={moreMenu.isOpen}
          onClose={moreMenu.handleClose}
          anchor={button.current}
        />
      )}
    </>
  );
};
