import React from "react";
import { Wheel } from "../Wheel";
import { Reward } from "../../";
import footer from "../../assets/spin-footer.svg";
import css from "./style.module.scss";
import clsx from "clsx";

interface Props {
  items: Reward[];
  position: number | null;
}

export const WheelContainer = ({ items, position }: Props) => {
  return (
    <div className={css.root}>
      <Wheel items={items} rotate={position} />
      <img
        className={clsx(css.footer, { [css.footer_show]: position !== null })}
        src={footer}
      />
    </div>
  );
};
