import React from "react";
import { useModalLocation } from "hooks";

import { Typography } from "components";

import { Description } from "../Description";
import { Title } from "../Title";

import css from "./style.module.scss";

interface Props {
  onButtonClick: () => void;
  className?: string;
}

export const BoardContent = ({ onButtonClick }: Props) => {
  const { pushHistoryTo } = useModalLocation("register");

  const handleButtonClick = () => {
    onButtonClick();
    pushHistoryTo({});
  };
  return (
    <div className={css.container}>
      <div className={css.title}>
        <Title />
      </div>
      <Description />
      <button type="button" className={css.button} onClick={handleButtonClick}>
        <div>
          <Typography className={css.buttonText}>Sign Up</Typography>
        </div>
      </button>
    </div>
  );
};
