import React, { useState, FunctionComponent, useEffect } from "react";

import { useLocalStorage } from "hooks";

import Context, { User } from "./Context";
import { coinsManager } from "hooks/useCoins/Emitter";
import { fetchBase } from "utils";

interface RequestUserData {
  success: boolean;
  message: string;
  notifications_count: number;
  eligible_daily_wheel: boolean;
  user_id: string;
  user_name: string;
  user_email: string;
  profile_picture_url: string;

  bad_token?: boolean;
}

const userLocalName = "user";

const UserProvider: FunctionComponent = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [token, setToken] = useLocalStorage("token");
  const [tfa] = useLocalStorage("2fa");

  const saveUser = (user: User | null) => {
    setUser(user);

    if (user) {
      localStorage.setItem(userLocalName, JSON.stringify(user));
    } else {
      localStorage.removeItem(userLocalName);
    }
  };

  const requestUserInfo = async () => {
    const { data } = await fetchBase<RequestUserData>("account/get-user-info", {
      variables: { token },
    });

    if (data && !data?.bad_token) {
      const { notifications_count, profile_picture_url, ...dataUser } = data;
      let avatarUrl = "";
      if (profile_picture_url) {
        avatarUrl = localStorage.getItem("avatarUrl") || "";
        if (avatarUrl !== profile_picture_url) {
          localStorage.setItem("avatarUrl", profile_picture_url);
        }
      } else {
        localStorage.removeItem("avatarUrl");
      }

      saveUser({
        ...dataUser,
        avatar: avatarUrl,
      });
      coinsManager.init();
    } else if (data?.bad_token) {
      saveUser(null);
      setToken("");
    }
  };

  useEffect(() => {
    if (!token || tfa) {
      if (!token) {
        saveUser(null);
      }
      return;
    }

    requestUserInfo();
  }, [token, tfa]);

  return (
    <Context.Provider
      value={{
        user,
        setUser: saveUser,
      }}>
      {children}
    </Context.Provider>
  );
};

export default UserProvider;
