import React, { useMemo, Suspense, useEffect, useState } from "react";
import { Route, Switch, useLocation } from "react-router-dom";

import {
  useLocalStorage,
  useModalLocation,
  useTheme,
  useUtmSource,
} from "hooks";
import { whenDocumentReady } from "utils";

import Page from "containers/Page";
import Menu from "containers/Menu";

import { routes } from "config/routes";

import FallbackPage from "components/fallbacks/Game";
import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";

import "config/themes/_default.scss";
import "config/themes/_white.scss";
import css from "./style.module.scss";

export interface Channel {
  id: string;
  flag: string;
  name: string;
}
interface IProps {
  appLocation: any;
}
// const independentModals = ["/reset-password", "/confirm-email", "/changeemail"];

export const Layout = ({ appLocation }: IProps) => {
  const [isLoaded, setIsLoaded] = useState(false);
  useTheme();

  const [tfa] = useLocalStorage("2fa");
  const { pushHistoryTo } = useModalLocation("tfa");
  useEffect(() => {
    if (tfa) {
      pushHistoryTo({});
    }
  }, [tfa]);

  const pathName = useMemo(() => {
    return appLocation?.state?.background?.pathname || appLocation?.pathname;
  }, [appLocation]);

  useEffect(() => {
    liveStatsManager.reset();
  }, [pathName]);

  useEffect(() => {
    whenDocumentReady(() => {
      setIsLoaded(true);
    });
  }, []);
  useUtmSource();

  return (
    <>
      <Menu />
      <div className={css.root}>
        <div className={css.background}>
          <Page>
            {!isLoaded ? (
              <FallbackPage />
            ) : (
              <Suspense fallback={<FallbackPage />}>
                <Switch>
                  {routes.map(({ path, exact, component }, i) => {
                    return (
                      <Route
                        key={`@customRoutes/${i}_${path}`}
                        path={path}
                        exact={exact}
                        component={component}
                      />
                    );
                  })}
                </Switch>
              </Suspense>
            )}
          </Page>
        </div>
      </div>
    </>
  );
};

export default Layout;
