import React from "react";
import clsx from "clsx";
import { useGetLocale } from "providers";

import { useLocalStorage } from "hooks";
import { Button, notifyBarManager } from "components";

import { useNotification } from "providers/Notification";
import { ReactComponent as RingIcon } from "assets/icons/ringer.svg";

import css from "./style.module.scss";

const Ringer = () => {
  const { notification, setNotification, missed, missedChallenges } =
    useNotification();

  const intl = useGetLocale();
  const [token] = useLocalStorage("token");

  const handleClickOnRing = () => {
    if (token) {
      setNotification({
        ...notification,
        isOpen: !notification.isOpen,
      });
    } else {
      notifyBarManager.error({
        timeOut: 2000,
        message: intl.formatMessage({ id: "crashInterface.messages.noLogin" }),
      });
    }
  };
  return (
    <span className={css.root} onClick={handleClickOnRing}>
      {!!missed && <span className={css.notification}>{missed}</span>}
      <Button
        icon
        style={{ width: 48, height: 50 }}
        waveColor="black"
        className={css.button}>
        <RingIcon
          className={clsx(
            css.desktop,
            css.ring,
            missedChallenges && css.missed,
          )}
        />
      </Button>
    </span>
  );
};

export default Ringer;
