import React, { FC, useEffect, useState } from "react";

import { createSuspenseComponent } from "utils";
import { NotificationElementProps } from "./Notification/types";
import notifyBarManager, {
  Notification as NotificationInterface,
} from "./notifybar-manager";
import styles from "./style.module.scss";

const Notification = createSuspenseComponent<NotificationElementProps>(
  import("./Notification"),
);

const NotifyBar: FC = () => {
  const [notifications, setNotifications] = useState<NotificationInterface[]>(
    [],
  );
  const [, setRemovedNotificationId] = useState(0);

  useEffect(() => {
    notifyBarManager.addChangeListener((notifications) =>
      setNotifications(notifications),
    );
    notifyBarManager.addRemoveListener((id) => setRemovedNotificationId(id));
    return () => notifyBarManager.removeNotifyListeners();
  }, []);

  return (
    <div className={styles.notifyBar}>
      {!!notifications.length &&
        notifications.map((notification) => {
          return (
            <Notification
              key={notification.id}
              onClose={() => {
                notifyBarManager.deleteNotificationRequest(notification.id);
              }}
              message={notification.message}
              type={notification.type}
              title={notification.title}
              removeNotification={notifyBarManager.isRemovingNotification(
                notification.id,
              )}
              onCloseAnimationEnd={() => {
                notifyBarManager.deleteNotification(notification.id);
              }}
            />
          );
        })}
    </div>
  );
};

export default NotifyBar;
