import React, { Suspense } from "react";

import { useMenu, useWhiteTheme } from "providers";
import { createSuspenseComponent } from "utils";
import { LazyHider } from "components";

import { EventDecoration } from "./EventDecoration";
import { ContentProps } from "./types";
import css from "./style.module.scss";

export interface BannerGameItemProps {
  className?: string;
  onClose?: () => void;
}

export const Content = createSuspenseComponent<ContentProps>(
  import("./content"),
);

const OnboardingPage = () => {
  const { setOpen, isOpen } = useMenu();
  const { activeEvent } = useWhiteTheme();

  return (
    <div className={css.root}>
      <div className={css.wrapper}>
        <LazyHider
          sizes={{
            height: "100vh",
          }}
          manualSet={isOpen}>
          <Content
            {...{
              activeEvent,
              setOpen,
            }}
          />
        </LazyHider>
        {activeEvent && (
          <Suspense fallback={null}>
            <EventDecoration eventName={activeEvent} />
          </Suspense>
        )}
      </div>
    </div>
  );
};

export default OnboardingPage;
