import React, { FunctionComponent, CSSProperties } from "react";
import { useMenu } from "providers/Menu";

import { SwipeableDrawer } from "components";

import classes from "./style.module.scss";

export const drawerWidth = "100%";

const boxStyle: CSSProperties = { boxSizing: "content-box" };

const ResponsiveMenu: FunctionComponent = ({ children }) => {
  const { isOpen, setOpen } = useMenu();

  return (
    <div style={boxStyle}>
      <SwipeableDrawer
        isOpen={isOpen}
        onClose={() => setOpen(false)}
        onOpen={() => setOpen(true)}>
        {children}
      </SwipeableDrawer>
    </div>
  );
};

export default ResponsiveMenu;
