import React, { useState, useEffect, CSSProperties } from "react";
import clsx from "clsx";
import css from "./style.module.scss";
import light from "../../assets/light.png";
import arrow from "../../assets/pasino-arrow.svg";
import logo from "../../assets/pasino-logo.svg";
import dailyWinArrow from "../../../DailyWin/assets/arrow.webp";
import { Reward } from "../../index";

interface Props {
  items: Reward[];
  rotate: number | null;
  isDailyWin?: boolean;
}
const colors = ["#F5C12C", "#F3256F", "#15BC7E", "#F12E33"];

export const Wheel = ({ items, rotate, isDailyWin }: Props) => {
  const [selected, setSelected] = useState<number | null>(null);
  const dailyWin = isDailyWin ? "dailyWin" : "";

  useEffect(() => {
    if (rotate !== null) {
      setSelected(rotate);
    }
  }, [rotate]);

  return (
    <div className={clsx(css.firstBorder, css[dailyWin])}>
      <div className={clsx(css.secondBorder, css[dailyWin])}>
        <div className={clsx(css.thirdBorder, css[dailyWin])}>
          <div className={clsx(css.wheelContainer, css[dailyWin])}>
            <div
              className={clsx(
                css.wheel,
                selected !== null && css.spining,
                css[dailyWin],
              )}
              style={
                {
                  "--nb-item": items.length,
                  "--selected-item": selected,
                } as CSSProperties
              }>
              {items.map((item, index) => (
                <div
                  className={clsx(
                    isDailyWin ? css.wheelItemDailyWin : css.wheelItem,
                  )}
                  key={index}
                  style={
                    {
                      "--item-nb": index,
                      "--item-back-col": colors[index % 4],
                    } as CSSProperties
                  }>
                  <div className={clsx(css.itemWrapper, css[dailyWin])}>
                    <p>{item.reward_amount.slice(0, 6)}</p>
                    <img
                      className={clsx(css.coin, css[dailyWin])}
                      src={`https://cdn.pasino.com/coins/${item.reward_coin.toLowerCase()}.png`}
                      alt="coin logo"
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      <div
        className={clsx(css.lightsContainer, selected !== null && css.spining)}
        style={
          {
            "--nb-light": items.length,
            "--selected-light": selected,
          } as CSSProperties
        }>
        {items.map((item, index) => (
          <div
            className={css.lightWrapper}
            style={{ "--light-nb": index } as CSSProperties}
            key={index}>
            <img
              className={clsx(css.light, index % 2 && css.lightD)}
              src={light}
              alt="star"
            />
          </div>
        ))}
      </div>
      <img
        className={clsx(css.arrow, css[dailyWin])}
        src={dailyWin ? dailyWinArrow : arrow}
        alt="arrow"
      />
      {!isDailyWin && <img className={css.arrow} src={logo} alt="logo" />}
    </div>
  );
};
