import React, { useRef, useEffect } from "react";
import backgroundImg from "./assets/timer-background.png";
import lightImg from "./assets/light.png";

interface Props {
  start: boolean;
  next_spin_time: number | null;
}

export const Timer = ({ start, next_spin_time }: Props) => {
  const canvasRef = useRef(null);
  let canvas: any;
  let ctx: CanvasRenderingContext2D;

  const background = new Image();
  const light = new Image();

  const repaint = (coefficient: number, time: number) => {
    let cx = canvas.width / 2,
      cy = canvas.height / 2;
    ctx.clearRect(0, 0, canvas.width, canvas.height);
    ctx.save();

    ctx.globalAlpha = coefficient;
    let width = background.width * coefficient;
    let height = background.height * coefficient;
    ctx.drawImage(background, cx - width / 2, cy - height / 2, width, height);

    const left = time - performance.now();
    const days = Math.floor((left / (1000 * 60 * 60)) % 24);
    const minutes = Math.floor((left / 1000 / 60) % 60);
    let seconds = Math.floor((left / 1000) % 60);
    const timeText = `${days} : ${minutes} : ${
      seconds < 10 ? `0${seconds}` : seconds
    }`;

    ctx.fillStyle = "#FFFFFF";
    ctx.font = "bold " + 46 * coefficient + "px Gilroy";
    ctx.textAlign = "center";
    ctx.fillText(timeText, cx, cy + 25);

    const timeStamp = performance.now();
    const group1 = (Math.sin(timeStamp / 500) + 1) / 2;
    const group2 = (Math.sin((timeStamp - 500) / 500) + 1) / 2;
    const group3 = (Math.sin((timeStamp - 750) / 500) + 1) / 2;

    const group1Size = light.width * group1 + 5;
    const group2Size = light.width * group2 + 5;
    const group3Size = light.width * group3 + 5;

    ctx.drawImage(
      light,
      cx - group1Size / 2,
      canvas.height - 15 - group1Size / 2,
      group1Size,
      group1Size,
    );
    ctx.drawImage(
      light,
      cx - 25 - group2Size / 2,
      canvas.height - 15 - group2Size / 2,
      group2Size,
      group2Size,
    );
    ctx.drawImage(
      light,
      cx + 25 - group2Size / 2,
      canvas.height - 15 - group2Size / 2,
      group2Size,
      group2Size,
    );
    ctx.drawImage(
      light,
      cx - 50 - group3Size / 2,
      canvas.height - 15 - group3Size / 2,
      group3Size,
      group3Size,
    );
    ctx.drawImage(
      light,
      cx + 50 - group3Size / 2,
      canvas.height - 15 - group3Size / 2,
      group3Size,
      group3Size,
    );

    ctx.restore();
  };

  const startAnimation = (endTime: number, duration: number) => {
    let start = performance.now();
    const frame = () => {
      let now = performance.now();
      const coefficient = (now - start) / duration;

      repaint(coefficient < 1 ? coefficient : 1, endTime);
      requestAnimationFrame(frame);
    };
    requestAnimationFrame(frame);
  };

  useEffect(() => {
    if (start) {
      startAnimation(next_spin_time!, 500);
    }
  }, [start]);

  useEffect(() => {
    canvas = canvasRef.current;
    ctx = canvas.getContext("2d");
    Promise.all([
      initImage(background, backgroundImg),
      initImage(light, lightImg),
    ]);
  });

  const initImage = async (img: any, src: string) => {
    return new Promise((resolve) => {
      img.src = src;
      img.onload = () => {
        resolve(true);
      };
    });
  };

  return <canvas width={360} height={140} ref={canvasRef} />;
};
