import { ModalRouteType, ModalsProps } from "config/routes";
import * as H from "history";

interface GetModalsProps extends Omit<ModalRouteType, "Component"> {
  token: string | null;
  location: H.Location<unknown>;
  redirectCallback?: (isOpen: boolean) => void;
}

export const getModalsProps = ({
  path,
  modalSearch,
  isAuthorizedOnly,
  token,
  location,
  redirectCallback,
}: GetModalsProps): Omit<ModalsProps, "gameName"> => {
  let isOpen = false;
  let modalName = "";
  const params: URLSearchParams = new URLSearchParams(location.search);
  let code = params.get("code") || "";
  const paths = location.pathname.split("/").filter((v) => v);
  if (path) {
    if (path === location.pathname) {
      isOpen = true;
    }
  }

  if (modalSearch) {
    const { name, value } = modalSearch;
    let isModal = false;
    if (typeof value === "string") {
      isModal = paths.includes(name);
    }
    if (isModal && paths.includes(value)) {
      modalName = value;
      isOpen = true;
    }
  }

  if (isAuthorizedOnly && !token && isOpen) {
    redirectCallback && redirectCallback(true);
    isOpen = false;
  }

  return {
    isOpen,
    modalName,
    code,
    props: {},
  };
};
