import React, { useEffect } from "react";
import { useLocation } from "react-router-dom";

import { useBalanceAnimation } from "providers";

import css from "./style.module.scss";

export const UpdateBalance = () => {
  const { pathname } = useLocation();
  const { balanceUpdate, balanceDispatch } = useBalanceAnimation();

  const onAnimationEnd = (id: string) => {
    balanceDispatch({ type: "filter", id });
  };

  useEffect(() => {
    balanceDispatch({ type: "clean" });
  }, [pathname]);

  return (
    <>
      {balanceUpdate.map(({ value, id }) => (
        <div
          key={id}
          className={css.balanceElement}
          onAnimationEnd={() => onAnimationEnd(id)}>
          {value}
        </div>
      ))}
    </>
  );
};
