import React, { FC } from "react";

import { useDetectRobots } from "hooks";

import { SoundSystemControlProvider } from "components/SoundModal/Provider";
import HotKeysProvider from "components/HotKeys/Provider";

import NotificationProvider from "providers/Notification/Provider";
import WindowSizeProvider from "providers/WindowSize/Provider";
import OnlineProvider from "providers/Online/Provider";
import AuthProvider from "providers/Auth/Provider";
import MenuProvider from "providers/Menu/Provider";
import UserProvider from "providers/User/Provider";
import { ChatProvider } from "providers";

export const Providers: FC = ({ children }) => {
  const isRobot = useDetectRobots();

  if (isRobot) {
    return <>{children}</>;
  }

  return (
    <WindowSizeProvider>
      <AuthProvider>
        <MenuProvider>
          <OnlineProvider>
            <NotificationProvider>
              <UserProvider>
                <ChatProvider>
                  <HotKeysProvider>
                    <SoundSystemControlProvider>
                      {children}
                    </SoundSystemControlProvider>
                  </HotKeysProvider>
                </ChatProvider>
              </UserProvider>
            </NotificationProvider>
          </OnlineProvider>
        </MenuProvider>
      </AuthProvider>
    </WindowSizeProvider>
  );
};
