import React, { FC } from "react";
import clsx from "clsx";

import { NotificationType } from "../notifybar-manager";
import { ReactComponent as ErrorIcon } from "assets/icons/notify-error.svg";
import { ReactComponent as WarningIcon } from "./icons/warning.svg";
import { ReactComponent as SuccessIcon } from "./icons/success.svg";
import { ReactComponent as CloseIcon } from "./icons/cross.svg";
import { ReactComponent as InfoIcon } from "./icons/info.svg";
import { NotificationElementProps } from "./types";
import styles from "./style.module.scss";

const notificationsStyleTypesMap = {
  [NotificationType.ERROR]: {
    class: styles.error,
    icon: ErrorIcon,
  },
  [NotificationType.SUCCESS]: {
    class: styles.success,
    icon: SuccessIcon,
  },
  [NotificationType.WARNING]: {
    class: styles.warning,
    icon: WarningIcon,
  },
  [NotificationType.INFO]: {
    class: styles.info,
    icon: InfoIcon,
  },
};

const Notification: FC<NotificationElementProps> = ({
  title,
  type,
  message,
  onClose,
  onCloseAnimationEnd,
  removeNotification,
  className,
  ...props
}) => {
  const typeStyle = notificationsStyleTypesMap[type];
  const Icon = typeStyle.icon;

  return (
    <div
      className={clsx(styles.root, className, typeStyle.class, {
        [styles.remove]: removeNotification,
      })}
      onAnimationEnd={(event) => {
        if (event.animationName === styles.notifyBarLeaveAnimation) {
          onCloseAnimationEnd && onCloseAnimationEnd();
        }
      }}
      {...props}>
      <Icon className={styles.icon} />
      <div className={styles.messageContainer}>
        {title && <h1>{title}</h1>}
        <span>{message}</span>
      </div>
      {onClose && (
        <div className={styles.close} onClick={onClose}>
          <CloseIcon />
        </div>
      )}
    </div>
  );
};

export default Notification;
