import React, { FC, useEffect, useMemo, useRef, useState } from "react";

import {
  Icon,
  Typography,
  ClickAwayListener,
  Popper as PopperRoot,
  MenuItem,
  Divider,
  SwitchBox,
} from "components";
import { coinsManager, useCoins, useEventListener } from "hooks";
import { SelectOption } from "../../../../type";
import css from "./style.module.scss";
import clsx from "clsx";

interface Props {
  anchor: HTMLButtonElement | null;
  isOpen: boolean;
  handleClose: () => void;
}

const switchBoxClasses = {
  root: css.switchBoxRoot,
  switch: {
    track: css.switchThumb,
    toggle: css.switchToggle,
    root: css.switchRoot,
  },
  text: css.switchText,
};

export const Popper: FC<Props> = ({ anchor, isOpen, handleClose }) => {
  const root = useRef<HTMLDivElement>(null);
  const { coinsList, selectedCoin, showInUSD, coinsPrices } = useCoins();

  const options = coinsList?.map<SelectOption>((c) => {
    const { name, price } = coinsPrices[c.coin] || {};
    return {
      value: c.coin,
      label: c.balance,
      icon: c.image,
      price: price || "0",
      name: name || c.coin,
    };
  });

  const totalAmount = useMemo(() => {
    return (
      Math.floor(
        options.reduce((acm, { label, price }) => {
          return acm + Number(label) * Number(price || 0);
        }, 0) * 100,
      ) / 100
    );
  }, [options]);

  const handleClick = (e: MouseEvent) => {
    if (root.current && !root.current.contains(e.target as Node)) {
      if (anchor && anchor.contains(e.target as Node)) {
        return;
      }
      handleClose();
    }
  };

  useEffect(() => {
    if (anchor !== null) {
      document.addEventListener("click", handleClick, true);
    } else {
      document.removeEventListener("click", handleClick, true);
    }

    return () => document.removeEventListener("click", handleClick, true);
  }, [anchor]);

  return (
    <div className={css.root}>
      <PopperRoot
        recalculate
        backgroundColor="#0a0d18"
        isOpen={isOpen}
        anchor={anchor}
        position="bottom"
        className={css.popper}
        classes={{
          triangle: css.triangle,
        }}
        withTriangle
        fixed>
        <div className={css.container} ref={root}>
          <div className={css.header}>
            <span>Wallet</span>
            <Icon
              variant="close"
              fontSize={10}
              onClick={handleClose}
              className={css.close}
            />
          </div>
          <div className={css.itemsContainer}>
            {options?.map(({ value, label, icon, annex, price, name }) => [
              <MenuItem
                key={value}
                waveColor="white"
                asButton
                selected={value === selectedCoin?.coin}
                classes={{ selected: css.selectedItem }}
                onClick={() => {
                  coinsManager.changeSelectedCoin(value);
                  handleClose();
                }}
                className={clsx(css.fontFamily, css.menuItem)}>
                {icon && <img className={css.icon} src={icon} alt={value} />}
                <span className={css.coinName}>{name}</span>
                <div className={css.grow} />
                {showInUSD ? (
                  <div className={css.moneyBox}>
                    <span>${(Number(label) * Number(price)).toFixed(8)}</span>
                    <span className={css.coinValue}>
                      {Number(label).toFixed(8)}
                    </span>
                  </div>
                ) : (
                  <span className={css.coinValue}>
                    {Number(label).toFixed(8)}
                  </span>
                )}

                {annex && (
                  <Typography className={css.menuItemAnnex}>{annex}</Typography>
                )}
              </MenuItem>,
            ])}
          </div>
          <div className={css.controlBox}>
            <div className={css.switchBox}>
              <span className={css.switchTitle}>View in USD</span>

              <SwitchBox
                isChecked={showInUSD}
                switchHandleChange={(value) =>
                  coinsManager.changeShowInUSD(value)
                }
                text={{
                  off: "OFF",
                  on: "ON",
                }}
                classes={switchBoxClasses}
              />
            </div>
            <div className={css.amountBox}>
              <span className={css.amountBoxTitle}>Total amount</span>
              <span className={css.amountValue}>{totalAmount} USD</span>
            </div>
          </div>
        </div>
      </PopperRoot>
    </div>
  );
};
