import React, { MouseEventHandler } from "react";

import { Avatar, Button } from "components";
import { useUser } from "providers";
import MenuIcon from "assets/icons/profileMenuIcon.svg";

import Menu from "../Menu";
import css from "./style.module.scss";

const UserMore = () => {
  const { user } = useUser();
  const userAvatar = user?.avatar || localStorage.getItem("avatarUrl");

  const [anchorEl, setAnchorEl] = React.useState<
    (EventTarget & HTMLButtonElement) | null
  >(null);

  const handleClick: MouseEventHandler<HTMLButtonElement> = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <>
      <Button
        className={css.avatarButton}
        waveColor="black"
        onClick={handleClick}>
        <Avatar
          avatar={userAvatar}
          userName={user?.user_name}
          className={css.userIcon}
        />
        <img src={MenuIcon} className={css.menuIcon} alt="" />
      </Button>
      <Menu onClose={handleClose} isOpen={!!anchorEl} anchor={anchorEl} />
    </>
  );
};

export default UserMore;
