import React, { FC, useEffect, useRef, useState } from "react";
import CaretIcon from "assets/icons/caretWallet.svg";

import { Button, GetCoinImg } from "components";
import { coinsManager, ManagerListenerValues, useCoins } from "hooks";
import { useBalanceAnimation } from "providers";

import { Popper, UpdateBalance } from "./components";
import css from "./style.module.scss";
import clsx from "clsx";

interface Props {
  anchor: HTMLDivElement | null;
  withCoinName?: boolean;
}

export const CoinContainer: FC<Props> = ({ anchor, withCoinName = true }) => {
  const { selectedCoin, showInUSD } = useCoins();
  const [isOpen, setIsOpen] = useState(false);
  const isDocumentFocused = useRef(false);
  const oldValue = useRef(selectedCoin);
  const rootRef = useRef<HTMLButtonElement>(null);
  const { balanceDispatch } = useBalanceAnimation();

  const onBalanceUpdate = (value: string) => {
    balanceDispatch({
      type: "update",
      value: {
        value,
        id: Math.random().toString(),
      },
    });
  };

  const isUsd = showInUSD && selectedCoin?.coin !== "GEM";
  const balance = isUsd
    ? (
        Number(selectedCoin?.balance || 0) * Number(selectedCoin?.price || 0)
      ).toFixed(8)
    : selectedCoin?.balance;

  useEffect(() => {
    const removeListener = coinsManager.listener(
      (manager: ManagerListenerValues) => {
        const newBalance = Number(manager.selectedCoin?.balance || 0);
        const oldBalance = Number(oldValue.current?.balance || 0);

        if (newBalance === oldBalance) {
          return;
        }

        const delta = newBalance - oldBalance;
        if (delta > 0) {
          let deltaValue = `+\u00A0${delta.toFixed(8)}`;
          if (showInUSD && manager.selectedCoin?.coin !== "GEM") {
            deltaValue = `+\u00A0$${(
              Number(delta) * Number(manager.selectedCoin?.price || 0)
            ).toFixed(8)}`;
          }

          onBalanceUpdate(deltaValue);
        }

        oldValue.current = manager.selectedCoin;
      },
    );

    const onFocus = () => {
      isDocumentFocused.current = true;
    };

    const onBlur = () => {
      isDocumentFocused.current = false;
    };

    onFocus();
    window.addEventListener("focus", onFocus);
    window.addEventListener("blur", onBlur);

    return () => {
      balanceDispatch({ type: "clean" });

      window.removeEventListener("focus", onFocus);
      window.removeEventListener("blur", onBlur);
      removeListener();
    };
  }, []);

  return (
    <>
      <Button
        ref={rootRef}
        className={css.coinContainer}
        onClick={() => {
          setIsOpen(!isOpen);
        }}
        classes={{
          label: css.wrapper,
        }}>
        <span className={css.coinImgWrapper}>
          <GetCoinImg
            staticCoin
            coin={selectedCoin?.coin}
            className={css.coinImg}
          />
          {withCoinName ? selectedCoin?.coin : ""}
        </span>

        <span className={css.balanceWrapper}>
          {isUsd && "$ "}

          {balance ? Number(balance).toFixed(8) : null}
          <img
            className={clsx(css.caret, isOpen && css.revert)}
            src={CaretIcon}
            alt=""
          />
        </span>
      </Button>
      <UpdateBalance />

      <Popper
        isOpen={isOpen}
        anchor={rootRef.current}
        handleClose={() => setIsOpen(false)}
      />
    </>
  );
};
