import React, { FC } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import css from "./style.module.scss";

const BonusBannerText: FC = () => {
  const intl = useGetLocale();
  return (
    <div className={css.root}>
      <p className={clsx(css.textMin, css.text)}>
        {intl.formatMessage({ id: "banners.winBonuses.won" })}
      </p>
      <p className={clsx(css.textBig, css.text)}>
        {intl.formatMessage({ id: "banners.winBonuses.bonus" })}
      </p>
    </div>
  );
};

export default BonusBannerText;
