import React, { FC, useState } from "react";
import { useGetLocale, useNotification } from "providers";
import clsx from "clsx";

import { Button, GetCoinImg } from "components";
import { fetchBase } from "utils";

import css from "./style.module.scss";

interface ClaimFetch {
  message: string;
  notifications_count: number;
  success: boolean;
}

interface Props {
  isNew?: boolean;
  title: string;
  description: string;
  amount: string;
  coin: string;
  id: string;
}

const Notification: FC<Props> = ({
  isNew,
  coin,
  amount,
  description,
  title,
  id,
}) => {
  const intl = useGetLocale();
  const { setMissed } = useNotification();

  const [noViewed, setNoViewed] = useState(isNew);

  const handleClick = async () => {
    const { data } = await fetchBase<ClaimFetch>("notification/claim-reward", {
      variables: {
        id,
      },
    });
    if (data) {
      setMissed(data.notifications_count);
    }
    setNoViewed(false);
  };

  return (
    <div
      className={clsx(css.root, {
        [css.new]: noViewed,
      })}>
      <h1 className={css.title}>{title}</h1>

      <p className={css.text}>{description}</p>

      <div className={css.coinWrapper}>
        <GetCoinImg coin={coin.toUpperCase()} className={css.coinImg} />
        <p className={clsx(css.text, css.coinText)}>{amount}</p>
      </div>

      {noViewed ? (
        <Button className={css.button} onClick={handleClick}>
          {intl.formatMessage({ id: "notification.content.claim" })}
        </Button>
      ) : (
        <p className={css.disableButton}>
          {intl.formatMessage({ id: "notification.content.received" })}!
        </p>
      )}
    </div>
  );
};

export default Notification;
