import React, { useState } from "react";
import { useHistory } from "react-router-dom";

import { useChat, useNotification } from "providers";

import { MenuButton } from "./components";
import { ReactComponent as Chat } from "./assets/chat.svg";
import { ReactComponent as Dots } from "./assets/dots.svg";
import { ReactComponent as Prize } from "./assets/prize.svg";
import { ReactComponent as Ring } from "./assets/ring.svg";
import { ReactComponent as Logo } from "./assets/logo.svg";
import css from "./style.module.scss";

export const MobileNavigation = () => {
  const [isNavMoreOpen, setIsNavMoreOpen] = useState(false);

  const history = useHistory();
  const { setIsHide } = useChat();

  const { setNotification, missed } = useNotification();
  const closeNotes = () =>
    setNotification((old) => ({ ...old, isOpen: false }));
  const closeChat = () => setIsHide(true);

  const chatOnClick = () => {
    closeNotes();
    setIsHide((isHide) => !isHide);
  };
  const notesOnClick = () => {
    closeChat();
    setNotification((old) => ({ ...old, isOpen: !old.isOpen }));
  };

  const handleChangePath = (value: string) => () => {
    closeNotes();
    closeChat();
    history.push(value);
  };
  //#have modal link?
  return (
    <>
      <div className={css.root}>
        <div className={css.background} />

        <div className={css.wrapper}>
          <MenuButton icon={Chat} onClick={chatOnClick} />
          <MenuButton icon={Prize} onClick={handleChangePath("bonus")} />

          <MenuButton icon={Logo} logo onClick={handleChangePath("/")} />

          <MenuButton icon={Ring} value={missed} onClick={notesOnClick} />
          <MenuButton
            icon={Dots}
            onClick={() => setIsNavMoreOpen((old) => !old)}
            moreMenu={{
              isOpen: isNavMoreOpen,
              handleClose: () => setIsNavMoreOpen(false),
            }}
          />
        </div>
      </div>
    </>
  );
};
